@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;400;600;700;800;900&display=swap');
* {
	font-family: 'Inter', sans-serif;
}

body {
	background-color: #1b1c1d;
	color: #ddd8c2;
}
.navbar a {
	color: #ddd8c2;
	font-size: 25px;
	font-weight: bold;
}
.button {
	background-color: transparent;
	color: #ddd8c2;
	border: 2px solid #ddd8c2;
	padding: 10px 20px;
	border-radius: 30px;
}
.error {
	padding-top: 70px;
}
.error h1 {
	font-size: 60px;
	font-weight: 700;
	margin-top: 120px;
}
.error img {
	object-fit: cover;
	height: 500px;
	width: 750px;
	border-radius: 0px 30px 30px 0px;
	margin-top: 50px;
}
.error404 {
	padding-top: 70px;
}
.error404 img {
	object-fit: cover;
	height: 500px;
	width: 750px;
	border-radius: 30px 0px 0px 30px;
	margin-top: 50px;
}
.error404 h1 {
	font-size: 60px;
	font-weight: 700;
	margin-top: 120px;
}
